<template>
  <div class="trainCom1">
    <h1 class="headtitle">Data Processing</h1>
    <br>
    <el-form :label-position="labelPosition" :model="form" status-icon :rules="rules" ref="form" label-width="170px" class="demo-form">
      <!--choose sdf-->
      <el-form-item  label="Upload SDF" prop="sdfPath">
        <UploadCom :isSDF="true"
                   tipmsg="Upload the SDF file to provide the raw data set for the training"
                   @func="getSDF"
                   v-if="hackReset == true"
        ></UploadCom>
      </el-form-item>
      <!--data processing-->
      <!--HBD-->
      <el-form-item label="HBD less than" prop="HBD">
        <el-tooltip placement="top">
          <div slot="content">Number of Hydrogen Bond Donors less than
            <br>
            Default: 8</div>
          <el-input v-model.number="form.HBD"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--HBA-->
      <el-form-item label="HBA less than" prop="HBA">
        <el-tooltip placement="top">
          <div slot="content">Number of Hydrogen Bond Acceptors less than
            <br>
            Default: 8</div>
          <el-input v-model.number="form.HBA"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--MW-->
      <el-form-item label="MW less than" prop="MW">
        <el-tooltip placement="top">
          <div slot="content">The average molecular weight of the molecule less than
            <br>
            Default: 800</div>
          <el-input v-model.number="form.MW"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--rings-->
      <el-form-item label="rings greater than" prop="rings">
        <el-tooltip placement="top">
          <div slot="content">Symmetrized SSSR for a molecule greater than
            <br>
            Default: 1</div>
          <el-input v-model.number="form.rings"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--Linker length-->
      <el-form-item label="Linker_len greater than" prop="len">
        <el-tooltip placement="top">
          <div slot="content">Linker's shortest path Default: 1</div>
          <el-input v-model.number="form.len"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--HEV-->
      <el-form-item label="HEV greater than" prop="HEV">
        <el-tooltip placement="top">
          <div slot="content">GetNumHeavyAtoms Default: 2</div>
          <el-input v-model.number="form.HEV"></el-input>
        </el-tooltip>
      </el-form-item>

      <!--train-->
      <h1 class="headtitle">Training</h1>
      <br>
      <!--Whether to use Transfer Learning-->
      <el-form-item label-width="200px" label="Use Transfer Learning" prop="isTransfer">
        <el-tooltip placement="top">
          <div slot="content">
            Whether to use the Transfer learning model for training</div>
          <el-switch
              v-model="form.isTransfer"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="Yes"
              inactive-text="No">
          </el-switch>
        </el-tooltip>
      </el-form-item>
      <!-- Whether to use own model-->
      <el-form-item label-width="200px" label="Use your own model" v-if="form.isTransfer" prop="ifOwnModel">
        <el-tooltip placement="top">
          <div slot="content">
            Whether to upload your own Transfer learning model for training</div>
          <el-switch
              v-model="form.ifOwnModel"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="Yes"
              inactive-text="No">
          </el-switch>
        </el-tooltip>
      </el-form-item>
      <!--choose model if true  true-->
      <el-form-item  label="Upload Model" v-if="form.isTransfer && form.ifOwnModel" >
        <UploadCom :isSDF="false" tipmsg="Upload your own Transfer learning model for training" @func="getModel" v-if="hackReset == true"></UploadCom>
      </el-form-item>
      <!--Using the existing model  if true false-->
      <el-form-item v-if="form.isTransfer && !form.ifOwnModel" label="Other Models" prop="existing_model">
        <el-tooltip placement="top">
          <div slot="content">Select the model provided by the system</div>
          <el-select v-model="form.selected_model" >
            <el-option
                v-for="item in models"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-tooltip>
      </el-form-item>
      <!--train steps-->
      <el-form-item label="train steps" prop="train_steps">
        <el-tooltip placement="top">
          <div slot="content">Number of training steps
            <br>
            Default: 50000</div>
          <el-input v-model.number="form.train_steps"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--save_checkpoint_steps-->
      <el-form-item label="checkpoint" prop="save_checkpoint_steps">
        <el-tooltip placement="top">
          <div slot="content">Save a checkpoint every X steps
            <br>
            Default: 1000</div>
          <el-input v-model.number="form.save_checkpoint_steps"></el-input>
        </el-tooltip>
      </el-form-item>
      <!--batch size-->
      <el-form-item label="batch size" prop="batch_size">
        <el-tooltip placement="top">
          <div slot="content">Maximum batch size for training
            <br>
            Default: 64</div>
          <el-input v-model.number="form.batch_size"></el-input>
        </el-tooltip>

      </el-form-item>
<!--      &lt;!&ndash;encoder_type decoder_type&ndash;&gt;-->
<!--      <el-form-item label="encoder_type">-->
<!--        <el-tooltip placement="top">-->
<!--          <div slot="content">Type of encoder layer to use</div>-->
<!--          <el-input v-model="form.encoder_type" :disabled="true"></el-input>-->
<!--        </el-tooltip>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="decoder_type">-->
<!--        <el-tooltip placement="top">-->
<!--          <div slot="content">Type of decoder layer to use</div>-->
<!--          <el-input v-model="form.decoder_type" :disabled="true"></el-input>-->
<!--        </el-tooltip>-->

<!--      </el-form-item>-->

      <!--email-->
      <el-form-item label="Email" prop="email">
        <el-input v-model="form.email" placeholder="Please enter your email address"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">submit</el-button>
        <el-button @click="resetForm('form')">reset</el-button>
      </el-form-item>
    </el-form>
    <el-card shadow="always" v-show="ifrun">
      <h2>The child process is {{pid}}</h2>
    </el-card>
  </div>
</template>

<script>
import {getListen} from '@/api/Get'
import UploadCom from "@/components/UploadCom";
export default {
  name: "TrainCom1",
  components: {
    UploadCom
  },
  data() {
    var check = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Could not be empty'));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('Please enter a numeric value'));
        } else {
            callback();
          }
      }, 500);
    };
    var check_batchsize = (rule, value, callback) => {
      if (value>128) {
        return callback(new Error('Batch Size greater than 128 may cause CUDA out of memory'));
      }
      else {
        callback()
      }
    };

    return {
      labelPosition: 'left',
      pid:'',
      ifrun:false,//ifrun==true,show pid
      models: [
        { label: 'ChEMBL', value: 'ChEMBL_model_average.pt' },
        { label: 'ZINC', value: 'zinc_3000w.pt' }
      ],
      hackReset: true,
      form: {
        sdfPath:'',
        modelPath:'',
        HBD:8,
        HBA:8,
        MW: 800,
        rings: 1,
        len:1,
        HEV:2,
        isTransfer:true,//if true show Upload Model
        ifOwnModel:true,//if true use Own Model
        selected_model: 'ChEMBL_model_average.pt',
        train_steps: 50000,
        save_checkpoint_steps:1000,
        batch_size:64,
        email:''
      },
      rules: {
        // modelPath:[
        //   { validator: checkFile, trigger: 'blur' }
        // ],
        HBD:[
          { validator: check, trigger: 'blur' }
        ],
        HBA:[
          { validator: check, trigger: 'blur' }
        ],
        MW:[
          { validator: check, trigger: 'blur' }
        ],
        rings:[
          { validator: check, trigger: 'blur' }
        ],
        len:[
          { validator: check, trigger: 'blur' }
        ],
        HEV:[
          { validator: check, trigger: 'blur' }
        ],
        train_steps:[
          { validator: check, trigger: 'blur' }
        ],
        save_checkpoint_steps:[
          { validator: check, trigger: 'blur' }
        ],
        batch_size:[
          { validator: check, trigger: 'blur' },
          { validator: check_batchsize, trigger: 'blur' }
        ],

        email:[
          { required: true, message: 'Please enter your email address', trigger: 'blur' },
          { type: 'email', message: 'Please enter the correct email address', trigger: ['blur', 'change'] }
        ]
      }
    };
  },
  props: {
    url: String,
  },
  methods: {
    getSDF(data){
      this.form.sdfPath=data
      // console.log('sdf:   '+this.form.sdfPath);
    },
    getModel(data){
      this.form.modelPath=data
      // console.log('model:   '+this.form.modelPath);
    },

    submitForm(formName) {
      var checkS = this.checkSubmit()
      this.$refs[formName].validate((valid) => {
        if (valid && checkS) {
          // 发送请求
          // var a = this.checkSubmit();
          // console.log(a)
          getListen(this.url,this.form).then(res=>{
            // console.log(res);
            this.pid = res.pid+3
            this.ifrun = true
            this.$notify({
              title: 'Training is running',
              message: 'The child process is '+this.pid,
              type: 'success'
            });
          }).catch(err=>{
            // console.log(err);
            this.$alert(err, 'Run failed', {
              confirmButtonText: 'confirm',})
          })
        }else{
          this.$alert('Input information is incomplete or incorrect','Submit failed', {
            confirmButtonText: 'confirm',
            type: 'warning'})
          return false;
        }

        // } else {
        //   // console.log('error submit!!');
        //   this.$message({
        //     showClose: true,
        //     message: 'Input information is incomplete or incorrect',
        //     type: 'error'
        //   });
        //   return false;
        // }
      });
  },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true;
      });
    },
    checkSubmit() {
      if(this.form.sdfPath==''){
        return false;
      }
      else{
        if(this.form.isTransfer==true
            && this.form.ifOwnModel==true
            && this.form.modelPath==''){
          return false
        }else{
          return true
        }
      }
    }
  },

}
</script>

<style scoped>
.el-button{
  width:120px;
  height:40px;
  font-size:15px

}
.el-input{
  width:410px;
}
.el-card {
  width: 48%;
}
